/*
* 
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Row, Col, Empty, Icon, Tag, Input, Button, AutoComplete, Spin, Checkbox, Menu, Dropdown, Badge, message, Affix} from 'antd';
import * as  mobx from 'mobx';
import moment from 'moment';
import {Scrollbars} from 'react-custom-scrollbars';
import InfiniteScroll from 'react-infinite-scroller';
import DetailForm from '@/components/digitalOrg/DetailForm';
import DetailFormVisit from '@/components/digitalOrg/DetailFormVisit';
import DetailFormMonthlySharing from '@/components/digitalOrg/DetailFormMonthlySharing';
import DetailFormSummary from '@/components/digitalOrg/DetailFormSummary';
import DetailFormReplay from '@/components/digitalOrg/DetailFormReplay';
import DetailFormNews from '@/components/digitalOrg/DetailFormNews';
import emitter from "@/utils/events";
import {detectZoom} from "@/utils/utils";
import DigitalComment from "@/components/digitalOrg/DigitalComment";

@inject('DigtalOrgStore')
@observer
export default class SmartOrg extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DigtalOrgStore;
		this.state = {
			digtalTem: [], // 
			ratio: 100
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		let _this = this;
		// 
		_this.getTable();
		this.store.getJournalTypeList();
		this.store.getJournalTagList();
		this.store.getUserPermissionByAppCode();
		// 
		_this.emitter = emitter.addListener('journal', (journal) => {
			_this.getTable();
		});
        // sts
		this.props.DigtalOrgStore.getSts();

		window.addEventListener("resize", function() {
			_this.setState({
			   ratio: detectZoom()
		});}, false);
	}

	componentWillMount() {
		let _this = this;
		if (_this.emitter) {
			emitter.removeListener(_this.emitter);
		}
		window.removeEventListener("resize", function() {
			_this.setState({
				ratio: 100
			});}, false);
	}

	// 
	handleEdit = (param) => {
		let dataArray = mobx.toJS(this.store.maintableData).map((item) => Object.assign(item, {selested: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selested: true, isRead: true}))
		this.store.maintableData = Object.assign([], dataArray);
		this.store.selestedId = param.id;
		this.store.draftId = param.id;  // ID
		this.store.detemList = Object.assign({}, mobx.toJS(this.store.digtalTemData).filter(item=>item.type==param.type)[0]);
		this.store.temList = Object.assign({}, mobx.toJS(this.store.digtalTemData).filter(item=>item.type==param.type)[0]);
		this.store.getJournalById(param.id)
			.then((res) => {
				this.store.deData = Object.assign({}, res);
				//this.store.detailData = Object.assign({}, res);
			});
		// 
		this.store.getLikeUsersByJournalId();
		// 
		this.store.getCommentsByJournalId();
		this.store.getJournalIndicators();
		this.store.isEdit = false;  // 
		if (this.child&&this.child.handleCalc!=undefined&&typeof this.child.handleCalc == "function") {
			this.child.handleCalc();
		}
		this.store.getNotReadCount();
		this.store.getReadUsersByJournalId();
	}

	// 
	handleInfiniteOnLoad = () => {
		let dataArray = mobx.toJS(this.store.maintableData);
		if (this.store.maintotalCount === dataArray.length) {
			return;
		}
		this.store.loading = true;
		this.store.mainQuery.pageIndex = this.store.mainQuery.pageIndex + 1;
		this.store.getJournalMainPageListNew().then((res) => {
			const data = res;
			dataArray = dataArray.concat(data.body);
			this.store.maintableData = Object.assign([], dataArray);
			this.store.loading = false;
		});
	}

	handleClick = (type) => {
		this.store.mainQuery.orderType = type;
		this.store.mainQuery.readType = 0;
		this.store.mainQuery.pageIndex = 1;
		this.store.mainQuery.pageSize = 10;
		this.store.getJournalMainPageList().then((res) => {
			if (res) {
				if (mobx.toJS(this.store.maintableData).length > 0) {
					// 
					let param = mobx.toJS(this.store.maintableData)[0];
					this.handleEdit(param);
				} else {
					return false;
				}
			}
		});
	}

	// 
	onChangeState = (type, item, e) => {
		let newData = JSON.parse(JSON.stringify(this.state[type]));
		let index = newData.findIndex(vitem => vitem == item.type);
		const {checked} = e.target;
		if (index == -1) {  // 
			this.setState({
				[type]: [...newData, item.type]
			}, () => {
				// 
				this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
					type: Object.assign([], this.state[type]),
					pageIndex: 1,
					pageSize: 10
				});
				this.store.getJournalMainPageList();
			});
		} else {
			if (checked == false) { // 
				newData.splice(index, 1);
				this.setState({
					[type]: Object.assign([], newData)
				}, () => {
					// 
					this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
						type: Object.assign([], this.state[type]),
						pageIndex: 1,
						pageSize: 10
					});
					this.store.getJournalMainPageList();
				});
			}
		}
	}

	onSelect = (value) => {
		// 
		this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
			text: value,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getJournalMainPageList();
	}

	onChange = (value) => {
		// 
		this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
			text: value,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getJournalMainPageList();
	}

	onAsync = (type, id, index) => {
		this.store.syncAssignment(id).then((res) => {
			if (res) {
				message.success("");
				// 
				let detailData = JSON.parse(JSON.stringify(this.store.deData));
				let assignments = detailData[type];
				assignments[index].isSync = 1; // 
				this.store.deData = Object.assign(detailData, {
					[type]: assignments
				});
			}
		})
	}

	// 
	handleAddNew = (detail) => {
		this.store.saveAssignments(detail).then((res) => {
			if (res) {
				message.success("");

				// 
				this.store.getJournalById(this.store.selestedId)
				.then((res) => {
					this.store.deData = Object.assign({}, res);
					this.child.handleCalc();
				});
			}
		});
	}

	onRef = (ref) => {
		this.child = ref
	}

	onRef2 = (ref) => {
		this.child2 = ref
	}

	hanndleVis = () => {
		const {mainQuery} = this.store;
		const orderType = mobx.toJS(mainQuery).orderType;
		if (orderType==0) {
			this.handleClick(1);
		} else if (orderType==1) {
			this.handleClick(2);
		} else {
			this.handleClick(1);
		}
	}

	hanndleVis2 = () => {
		const {mainQuery} = this.store;
		const orderType = mobx.toJS(mainQuery).orderType;
		if (orderType==0) {
			this.handleClick(4);
		} else if (orderType==4) {
			this.handleClick(3);
		} else {
			this.handleClick(4);
		}
	}

	// 
	handleRoute = () => {
		this.store.digtalKey = "2";
		this.props.history.push("/digital/org");
	}

	// 
	handleDraftRoute = () => {
		this.store.digtalKey = 0;
		this.props.history.push("/digital/draft");
	}

	// 
	getTable = () => {
		this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
			saveType: 2,
			text: "",
			tag: [],
			type: [],
			orderType: 0,
			pageIndex: 1,
			pageSize: 10,
			category: 1,
			readType: 0
		});
		this.store.getJournalMainPageList().then((res) => {
			if (res) {
				if (mobx.toJS(this.store.maintableData).length > 0) {
					// 
					let param = mobx.toJS(this.store.maintableData)[0];
					this.handleEdit(param);
				} else {
					return false;
				}
			}
		});
	}

	// 
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
			try {
				let anchorElement = document.getElementById(anchorName);
				if (anchorElement) {
					anchorElement.scrollIntoView({block: 'center'});
				}
			} catch(err) {
				console.log("scrollToAnchorERR=>"+err);
			}
		}
	}

	// 
	checkoutType = (type, item) => {
		if (type=="1") { //
			this.setState({
				digtalTem: []
			}, () => {
				// 
				this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
					type: this.state.digtalTem,
					pageIndex: 1,
					pageSize: 10
				});
				this.store.getJournalMainPageList().then((res) => {
					if (res) {
						if (mobx.toJS(this.store.maintableData).length > 0) {
							// 
							let param = mobx.toJS(this.store.maintableData)[0];
							this.handleEdit(param);
						} else {
							return false;
						}
					}
				});
			});
		} else if (type=="3") {   // 
			this.setState({
				digtalTem: ["collection"]
			}, () => {
				// 
				this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
					type: this.state.digtalTem,
					pageIndex: 1,
					pageSize: 10
				});
				this.store.getJournalMainPageList().then((res) => {
					if (res) {
						if (mobx.toJS(this.store.maintableData).length > 0) {
							// 
							let param = mobx.toJS(this.store.maintableData)[0];
							this.handleEdit(param);
						} else {
							return false;
						}
					}
				});
			});
		} else {
			this.setState({
				digtalTem: [item.type]
			}, () => {
				// 
				this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
					type: this.state.digtalTem,
					pageIndex: 1,
					pageSize: 10
				});
				this.store.getJournalMainPageList().then((res) => {
					if (res) {
						if (mobx.toJS(this.store.maintableData).length > 0) {
							// 
							let param = mobx.toJS(this.store.maintableData)[0];
							this.handleEdit(param);
						} else {
							return false;
						}
					}
				});
			});
		}
	}

	// 
	checkreadType = () =>{
		let mainQuery = JSON.parse(JSON.stringify(this.store.mainQuery));
		if (mainQuery.readType==0) {
			// 
			this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
				readType: 1,
				pageIndex: 1,
				pageSize: 10
			});
			this.store.getJournalMainPageList().then((res) => {
				if (res) {
					if (mobx.toJS(this.store.maintableData).length > 0) {
						// 
						let param = mobx.toJS(this.store.maintableData)[0];
						this.handleEdit(param);
					} else {
						return false;
					}
				}
			});
		} else {
			// 
			this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
				readType: 0,
				pageIndex: 1,
				pageSize: 10
			});
			this.store.getJournalMainPageList().then((res) => {
				if (res) {
					if (mobx.toJS(this.store.maintableData).length > 0) {
						// 
						let param = mobx.toJS(this.store.maintableData)[0];
						this.handleEdit(param);
					} else {
						return false;
					}
				}
			});
		}
	}

	render() {
		const {digtalTem, ratio} = this.state;
		const {notReadCount, maintableData, loading, hasMore, maintotalCount, digtalTemData, mainQuery, tagList, detailData, hasJournal, temList, isEdit, deData, detemList} = this.store;
		const options = mobx.toJS(tagList).map(opt => opt.name);
		const menu = (
			<Menu>
				{mobx.toJS(digtalTemData).map((item, index) => {
					return <Menu.Item key={item.type}>
						<Checkbox
							checked={this.state["digtalTem"].includes(item.type)}
							key={item.type + "-" + index}
							onChange={this.onChangeState.bind(this, "digtalTem", item)}
						>{item.name}</Checkbox></Menu.Item>
				})}
			</Menu>
		);
		const orderType = mobx.toJS(mainQuery).orderType;
		const readType = mobx.toJS(mainQuery).readType;
		const temType = mobx.toJS(detemList).type;

		return (
			<Row className="w clearfix pr">
				{/*<Affix offsetTop={120}>*/}
					<ul className="smartorg_card">
						<li key={'digtalTemData'+"quanbu"}
                            className={digtalTem.length==0?"smartorg_card_active":""}
						    onClick={this.checkoutType.bind(this, '1')}></li>
						{mobx.toJS(digtalTemData).map((item, index) => {
							return <li key={'digtalTemData'+index}
							           className={digtalTem.length>0&&digtalTem.includes(item.type)?"smartorg_card_active":""}
							           onClick={this.checkoutType.bind(this, '2', item)}>{item.name}</li>
						})}
						<li key={'digtalTemData'+"shoucang"}
						    className={digtalTem.length>0&&digtalTem.includes("collection")?"smartorg_card_active":""}
						    onClick={this.checkoutType.bind(this, '3')}></li>
					</ul>
				{/*</Affix>*/}
				<div className="fl" style={{paddingRight: '16px', width: '330px', height: "calc(100vh - 180px)"}}>
					<div className="clearfix"
					     style={{
						display: 'flex',
						alignItems:'center',
						justifyContent: 'flext-start',
						padding: '0 0 10px 0',
						borderBottom: '1px solid #e6e6e6'}}>
						<Button size="small" type="link" style={{padding: '0 2px'}} onClick={this.hanndleVis}></Button>
						<span style={{display: 'inline-block', width: '21px'}}>
							<Icon onClick={this.handleClick.bind(this, 2)} type="caret-up" className={orderType==1?"caret-up digital_icon_active":"digital_icon caret-up"}/>
	                        <Icon onClick={this.handleClick.bind(this, 1)} type="caret-down" className={orderType==2?"caret-down digital_icon_active":"digital_icon caret-down"}/>
						</span>
						<Button size="small" type="link" style={{marginLeft:'1px', padding: '0 2px'}} onClick={this.hanndleVis2}></Button>
						<span style={{display: 'inline-block', width: '21px'}}>
							<Icon onClick={this.handleClick.bind(this, 3)} type="caret-up" className={orderType==4?"caret-up digital_icon_active":"digital_icon caret-up"}/>
	                        <Icon onClick={this.handleClick.bind(this, 4)} type="caret-down" className={orderType==3?"caret-down digital_icon_active":"digital_icon caret-down"}/>
						</span>
						<span
							onClick={this.checkreadType}
							style={{textAlign: 'center',color: readType==0?"#666":"#1E91FF", display: 'inline-block', width: '70px', fontSize: '14px', cursor: 'pointer'}}>
							({notReadCount>=99?"99+":notReadCount})</span>
						<AutoComplete
							className="certain-category-search fr"
							dropdownClassName="certain-category-search-dropdown"
							dropdownMatchSelectWidth={false}
							dropdownStyle={{width: '125px'}}
							size="middle"
							style={{width: '145px', marginLeft: '6px'}}
							dataSource={options}
							placeholder=""
							optionLabelProp="value"
							onSelect={this.onSelect}
							onSearch={this.onChange}
						>
							<Input suffix={<Icon type="search" className="certain-category-icon"/>}/>
						</AutoComplete>
					</div>
					{/*<Affix offsetTop={60}>*/}
					<div className="require-table" style={{height: "calc(100% - 63px)"}}>
						<Scrollbars
							style={{
								height: "calc(100% - 10px)"
								//minHeight: "100px",
								//height: "calc(100vh - "+ ratio<100?(300+3*ratio):(180)+"px)"
							}}
							autoHide={false}>
							{mobx.toJS(maintableData).length > 0 ?
								<InfiniteScroll
									initialLoad={false}
									pageStart={0}
									loadMore={this.handleInfiniteOnLoad.bind(this)}
									hasMore={!loading && hasMore}
									useWindow={false}
								>
									{mobx.toJS(maintableData).length > 0 && mobx.toJS(maintableData).map((item, index) => {
										return (<div
											className={item.selested == true ? "require-tr require-tr-active" : "require-tr"}
											onClick={this.handleEdit.bind(this, item)}
											key={'tab-' + index}>
											<span
												className={item.isRead == true ? "require-td" : "require-td active"}
												title={item.title}>{item.title}</span>
											<span className="require-title">
												<span className="mr5">{item.userName}</span>
												{item.tags == null ? null : item.tags.map((item, index) => {
													return <Tag
														className={index >= 2 ? "hidden" : ""}
														color="gold">{item.name}</Tag>
												})}
											</span>
											<span className="require-lavel">{moment(item.actualTime).format("YYYY-MM-DD")}</span>
										</div>)
									})}
									{loading && hasMore && (
										<div className="demo-loading-container">
											<Spin/>
										</div>
									)}
								</InfiniteScroll> : <Empty imageStyle={{marginTop: "50px"}}/>}
						</Scrollbars>
					</div>
					<div style={{
						fontSize: '12px',
						color: 'rgb(153, 153, 153)'
					}}>{` ${maintotalCount}  `}</div>
					{/*</Affix>*/}
				</div>
				<div className="fl" style={{width: 'calc(100% - 350px)', borderLeft: '1px solid #EBEBEB', minHeight: '350px', paddingLeft: '15px',
					overflowY: 'scroll', height: "calc(100vh - 180px)"
				}}>
					{temType==="meeting"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailForm
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{temType==="visit"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailFormVisit
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{temType==="monthlySharing"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailFormMonthlySharing
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{temType==="summary"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailFormSummary
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{temType==="replay"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailFormReplay
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{temType==="news"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> :
						<DetailFormNews
							store={this.store}
							detailData={mobx.toJS(deData)}
							onAsync={this.onAsync}
							hasJournal={hasJournal}
							handleAddNew={this.handleAddNew}
							onRef={this.onRef}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
							scrollToAnchor={this.scrollToAnchor}
						/>:null}
					{/**/}
					{mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/>:!isEdit?<DigitalComment
						store={this.store}
					/>:null}
				</div>
			</Row>
		);
	}
}





